import React, { useState, useEffect, useContext } from "react";
import "./Cart.css";
import { data, Route, Routes, useLocation, useNavigate } from "react-router-dom";
import { postData, serverURL } from "../../services/NodeServices";
import AddressComp from "./AddressComp/AddressComp";
import { MyContext } from "../../context/MyContext";
import AddAddress from "./AddAddressForm/AddAddress";

export default function Cart() {
  const navigate = useNavigate();
  const { cart, setCart,user ,isloggedIn} = useContext(MyContext);
  const [myaddress,setMyaddress]=useState('')
  const [bill, setBill] = useState(false);
  const [loading ,setLoading]=useState(true)


  const loadRazorpayScript = () => {
    return new Promise((resolve, reject) => {
      const script = document.createElement("script");
      script.src = "https://checkout.razorpay.com/v1/checkout.js";
      script.onload = () => resolve(true);
      script.onerror = () =>
        reject(new Error("Failed to load Razorpay script"));
      document.body.appendChild(script);
    });
  };


  const handleSubmit = async () => {


  /*   if(!validate()){
      return
    } */

    

    loadRazorpayScript();

    const response = await fetch(`${serverURL}create-order`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ amount: bill.finalPrice }),
    });

    const order = await response.json();

    const options = {
      key: "rzp_live_ihXhp7al0XuYsU",
     /*    key:"rzp_test_nt89OO93hs7lOa", */
      amount: order.amount,
      currency: order.currency,
      name: "Products",
      description: "Payment for buying shirt",
      order_id: order.id,
      handler: async (paymentResponse) => {
        console.log(paymentResponse);

        const verifyResponse = await fetch(`${serverURL}verify-payment2`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            payment_id: paymentResponse.razorpay_payment_id,
            order_id: paymentResponse.razorpay_order_id,
            signature: paymentResponse.razorpay_signature,
            data:{list:cart,bill:bill,address:myaddress}
         
          }),
        });

        const verificationResult = await verifyResponse.json();
        console.log(verificationResult);

        if (verificationResult.status) {
           
          setCart([])
         
         

          setTimeout(() => {
            navigate("/");
          }, 511);
        } else {
         
        }
      },
      prefill: {
        name: "User Name",
        email: "user@example.com",
      },
      theme: {
        color: "#3399cc",
      },
    };

    const razorpay = new window.Razorpay(options);
    razorpay.open();
  };








  useEffect(() => {
    generateBill();
  }, []);
  const [mycart, setMyCart] = useState(cart);

  const truncateText = (text, maxLength) => {
    return text.length > maxLength
      ? text.substring(0, maxLength) + "..."
      : text;
  };

  const [topProducts, setTopProducts] = useState(
    JSON.parse(localStorage.getItem("cart") || "[]")
  );
  const [page, setPage] = useState(1);

  const handleRemove = (variant_id) => {
    // Filter out the item to remove from the cart
    const updatedCart = topProducts.filter(
      (item) => item.variant.variant_id !== variant_id
    );
    setCart(updatedCart); // Update the context cart state
    setTopProducts(updatedCart); // Update the local state as well
  };

  const generateBill = () => {
    let totalPrice = 0;
    let shippingPrice = 50; // Example shipping price, you can adjust this as needed

    // Map each product to the required bill structure
    const billItems = topProducts.map((item) => {
      const productDetail = `${truncateText(
        item.variant.variantname,
        61
      )}, Size: ${item.variant.size}, Color: ${item.variant.color}`;
      const productPrice = item.variant.price * item.quantity; // Assuming price is per unit and quantity is specified

      totalPrice += productPrice;

      return {
        product: productDetail,
        size: item.variant.size,
        color: item.variant.color,
        price: productPrice,
      };
    });

    // Create the final structure with 'items', shipping, and final price
    const bill = {
      items: billItems,
      shippingPrice: 75,
      finalPrice: totalPrice + shippingPrice,
    };

    setBill(bill); // Update the state with the new bill structure
  };
  const location = useLocation();

  const handleContinue = () => {
    if (location.pathname == "/cart"&&isloggedIn) {
      navigate("/cart/select-address");
    } else if (location.pathname == "/cart/select-address" && myaddress) {
      handleSubmit()
    }
    console.log(bill);
  };

  const ProductList = () => {
    return (
      /*   {page==1?  <div className="cart__cards"> */
      <>
      
        {topProducts.length != 0 ?
          topProducts?.map((item) => (
            <div
              className="cart__card"
              key={item.variant.product_id}
              style={{ position: "relative" }}
            >
              <div className="cart_card">
                <img
                  src={`${serverURL}images/${
                    item.variant.image_url.split(",")[0]
                  }`}
                  alt=""
                />
                <div className="cart__carddata">
                  <p className="carttitle">{item.variant.variantname}</p>
                  <div style={{ display: "flex", alignItems: "center" }}>
                    <p>₹ {item.variant.price}</p>
                    <p>₹ {item.variant.price}</p>
                  </div>
                  <div
                    className="cart__quantity"
                    style={{ display: "flex", alignItems: "center" }}
                  >
                    <p>Quantity:</p> <p>{item.quantity}</p>
                  </div>
                  <div
                    className="cart__quantity"
                    style={{ display: "flex", alignItems: "center" }}
                  >
                    <p>Size</p> <p>{item.variant.size}</p>
                  </div>
                </div>
              </div>
              <div
                className="cartedit"
                onClick={() => {
                  handleRemove(item.variant.variant_id);
                }}
              >
              <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M7 1H13M1 4H19M17 4L16.2987 14.5193C16.1935 16.0975 16.1409 16.8867 15.8 17.485C15.4999 18.0118 15.0472 18.4353 14.5017 18.6997C13.882 19 13.0911 19 11.5093 19H8.49065C6.90891 19 6.11803 19 5.49834 18.6997C4.95276 18.4353 4.50009 18.0118 4.19998 17.485C3.85911 16.8867 3.8065 16.0975 3.70129 14.5193L3 4M8 8.5V13.5M12 8.5V13.5" stroke="#F02759" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
</svg>

              </div>

              <div className="buttom__bill">

              </div>
            </div>
          )):
          <div className="cart__empty">
              <img src="/image/empty.png" />
              <p>Your cart is Empty</p>
             {/*  <button> Go to Home </button> */}
          </div>
          }
      </>
    );
  };

  return (
    <div className="cart__cont">
      <div className="cart__inner">
        <div className="cart__left">
          <Routes>
            <Route path="/add-address" element={<AddAddress />} />
            <Route path="/" element={<ProductList />} />
            <Route path="/select-address" element={<AddressComp address={myaddress} setAddress={setMyaddress} />} />
          </Routes>
        </div>
         {topProducts&&topProducts.length!=0?   <div className="cart__right">
          <div className="cartright__header">{`Price Details ( ${topProducts.length} Items) `}</div>

          {bill &&
            bill.items.map((item) => {
              return (
                <div className="cartright__details">
                  <p>{item.product} </p> <span>₹ {item.price}</span>{" "}
                </div>
              );
            })}
          {bill && bill.length != 0 ? (
            <div className="cartright__details">
              <p>{"Total Cost"} </p> <span>₹ {bill.finalPrice}</span>{" "}
            </div>
          ) : (
            <></>
          )}
          <div className="cartbutton">
            {isloggedIn?<button className="logbutton" onClick={handleContinue}>
              Continue
            </button>:<button className="logbutton" onClick={()=>{navigate('/log-in')}}>
              Please login to Continue
            </button>}
          </div>
        </div>:<></>}
      </div>
    </div>
  );
}
